import { Component, OnInit } from "@angular/core";
import { FormGroup, FormBuilder, Validators } from "@angular/forms";
import { ApiService } from "../../services/api.service";
import { Router } from "@angular/router";
import { ScreenOrientation } from "@ionic-native/screen-orientation/ngx";

import Swal from "sweetalert2";

@Component({
  selector: "app-login",
  templateUrl: "./login.page.html",
  styleUrls: ["./login.page.scss"],
})
export class LoginPage implements OnInit {
  userForm: FormGroup;
  user = this.api.getCurrentUser();
  processLogin = false;

  constructor(
    private api: ApiService,
    private fb: FormBuilder,
    private router: Router,
    private screenOrientation: ScreenOrientation
  ) {
    this.user.subscribe((user) => {
      if (user) {
        // this.loadPrivatePosts();
      } else {
        // this.posts = [];
      }
    });
  }

  ngOnInit() {
    this.screenOrientation.unlock();
    this.userForm = this.fb.group({
      username: ["", Validators.required],
      email: "",
      password: ["", Validators.required],
    });
  }

  login() {
    this.processLogin = true;
    this.api
      .signIn(this.userForm.value.username, this.userForm.value.password)
      .subscribe(
        (res) => {
          let resdata;
          resdata = res;
          this.processLogin = false;
          if (resdata.success === false) this.showError(res);
          this.router.navigate(["/home"]);
        },
        (err) => {}
      );
  }
  async showError(err) {
    Swal.fire("Invalid Login", "Username or password are invalid", "error");
  }
}
